.work {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: TrueNorth-Regular;
}

.grid {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
}

.one {
  grid-area: one;
}

.full {
  width: 100%;
  // height: 50vh;
  height: 75vh;
  justify-content: center;
  display: flex;
  flex: 1;
  img {
    // max-width: 100%;
    // object-fit: cover;
  }
  .project-desc-full {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    flex-direction: column;
    width: 100%;
    background-color: #121414;
    color: whitesmoke;
  }
  .project-desc {
    width: 100%;
    height: 75%;
    opacity: 0;
    position: absolute;
  }
}
img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.two {
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  align-items: center;
  flex-direction: row;
}

.half {
  width: 50%;
  height: 50vh;
  // border: 1px solid black;
  display: flex;
  // align-items: center;
  // justify-content: center;
  text-align: center;
  .project-desc {
    width: 50%;
    height: 50%;
    opacity: 0;
    position: absolute;
  }
}

.three {
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  align-items: center;
  flex-direction: row;
  .project-desc {
    width: 33%;
    height: 33%;
    opacity: 0;
    position: absolute;
  }
}

.third {
  // border: 1px solid black;
  width: 33.33%;
  height: 33vh;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    // width: 33.33%;
    height: 33.33vh;
    object-fit: cover;
  }
}

.project-desc:hover {
  background: rgba(238, 238, 238, 0.233);
  color: #fff;
  opacity: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease;
}

.project-title {
  font-weight: 700;
  font-size: 24px;
}

@media only screen and (max-width: 1024px) {
  .grid {
    display: flex;
    flex-direction: column;
    flex: 1;
    .one {
      width: 100%;
      // height: auto;
      .full {
        height: 50vh;
        img {
          width: 100%;
        }
        .project-desc {
          width: 100%;
          height: 50%;
        }
      }
    }
    .two {
      flex-direction: column;
      flex: 1;
      .half {
        width: 100%;
        // height: auto;
        img {
          width: 100%;
        }
        .project-desc {
          width: 100%;
        }
      }
    }
    .three {
      flex-direction: column;
      .third {
        width: 100%;
        height: 50vh;
        img {
          width: 100%;
          height: 100%;
        }
        .project-desc {
          width: 100%;
          height: 50%;
        }
      }
    }
  }
}
